/*Стили для раскрывающегося меню*/
@media (max-width: 660px) {
  .main-nav--opened .main-nav__wrapper {
    background-color: #283645; }
  .main-nav__toggle {
    display: block;
    position: relative;
    width: 50px;
    height: 24px;
    background-color: transparent;
    cursor: pointer;
    border: none;
    outline: none; } }
  @media (max-width: 660px) and (min-width: 660px) {
    .main-nav__toggle {
      margin-top: 17px; } }

@media (max-width: 660px) {
  .main-nav--closed .main-nav__toggle::before {
    content: "";
    position: absolute;
    top: -1px;
    right: 0;
    width: 50px;
    height: 4px;
    background-color: #ffffff;
    box-shadow: 0 10px 0 0 #ffffff, 0 20px 0 0 #ffffff; }
  .main-nav--closed .main-nav__toggle:active::before {
    background-color: fade(#ffffff, 30%);
    box-shadow: 0 8px 0 0 #ffffff, 0 16px 0 0 #ffffff; }
  .main-nav__list a {
    display: block;
    border-top: 1px solid rgba(255, 255, 255, 0.15);
    border-bottom: 1px solid #000000; }
  .main-nav__item--border-top {
    border-top: 1px solid #000000; }
  .main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
    content: "";
    position: absolute;
    top: 12px;
    right: -4px;
    width: 31px;
    height: 3px;
    background-color: #ffffff; }
  .main-nav--opened .main-nav__toggle::before {
    transform: rotate(45deg);
    box-shadow: none; }
  .main-nav--opened .main-nav__toggle::after {
    transform: rotate(-45deg); }
  .main-nav--opened .main-nav__toggle:active::before, .main-nav--opened .main-nav__toggle:active::after {
    background-color: fade(#ffffff, 30%); }
  .main-nav--closed .main-nav__item {
    display: none; }
  /*Пошла реализация мобильного меню без JS*/
  .main-nav--nojs .main-nav__toggle {
    display: none; }
  .main-nav--nojs .main-nav__inner {
    position: static;
    min-height: 0;
    margin-top: -11px; }
  .main-nav--nojs .main-nav__item {
    display: block; } }

@media (min-width: 960px) {
  .main-nav__toggle {
    display: none; } }

